"use client";

import { useEffect, useMemo, useState } from "react";

type ApiEnvelope<T> = {
  success: boolean;
  data: T;
  message?: string;
};

type WeatherResponse = {
  city: string;
  temperature: number;
  feelsLike: number;
  humidity: number;
  description: string;
  iconUrl: string;
  windSpeed: number;
};

type ExchangeRateResponse = {
  currency: string;
  baseRate: number;
  buyRate: number;
  sellRate: number;
  date: string;
};

type SpotResponse = {
  id: string;
  name: string;
  type: string;
  rating: number | null;
  address: string;
  photoUrl: string | null;
  lat: number;
  lng: number;
  distanceKm: number;
  reason: string;
};

type SpotDetailResponse = {
  id: string;
  name: string;
  type: string;
  rating: number | null;
  address: string;
  phone: string | null;
  website: string | null;
  openingHours: string[];
  photoUrls: string[];
  lat: number;
  lng: number;
};

type PhraseResponse = {
  id: number;
  category: string;
  korean: string;
  chinese: string;
  pronunciation: string;
};

const PHRASE_CATEGORIES: { value: string; label: string }[] = [
  { value: "airport", label: "Í≥µÌï≠" },
  { value: "transport", label: "ÍµêÌÜµ" },
  { value: "hotel", label: "ÏàôÏÜå" },
  { value: "restaurant", label: "ÏùåÏãùÏ†ê" },
  { value: "shopping", label: "ÏáºÌïë" },
  { value: "emergency", label: "Í∏¥Í∏âÏÉÅÌô©" },
];

type WeatherForecastItem = {
  date: string;
  minTemp: number;
  maxTemp: number;
  description: string;
  iconUrl: string;
};

type SpotFilters = {
  type: "restaurant" | "cafe" | "attraction";
  radius: string;
  openNow: boolean;
  minRating: string;
};

const INITIAL_FILTERS: SpotFilters = {
  type: "restaurant",
  radius: "5000",
  openNow: false,
  minRating: "",
};

function travelTime(distanceKm: number) {
  const walkMin = Math.max(1, Math.round((distanceKm / 4.5) * 60));
  const transitMin = Math.max(5, Math.round((distanceKm / 22) * 60));
  return { walkMin, transitMin };
}

async function requestApi<T>(url: string): Promise<T> {
  const response = await fetch(url, { cache: "no-store" });

  let payload: ApiEnvelope<T> | null = null;
  try {
    payload = (await response.json()) as ApiEnvelope<T>;
  } catch {
    throw new Error("ÏùëÎãµ ÌååÏã±Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
  }

  if (!response.ok || !payload.success) {
    throw new Error(payload.message || "ÏöîÏ≤≠Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
  }

  return payload.data;
}

function typeLabel(type: string) {
  if (type === "restaurant") return "ÎßõÏßë";
  if (type === "cafe") return "Ïπ¥Ìéò";
  return "Í¥ÄÍ¥ëÏßÄ";
}

